import { Component, OnInit } from '@angular/core';
import {ServerService} from '../server.service';
import {Person} from '../person';


@Component({
  selector: 'app-peoplelist',
  templateUrl: './peoplelist.component.html',
  styleUrls: ['./peoplelist.component.css']
})
export class PeoplelistComponent implements OnInit {
  peoplelist: Person[];
  selectedPerson:Person;
  errorMessage:string;


  constructor(private serverService:ServerService) { 

    this.update();
  }
  update(){
    this.selectedPerson=null;
    this.serverService.getPersonRest().subscribe(
      data=>this.peoplelist=data,
      error=>this.errorMessage=error);
  }
  onSelect(p:Person){
    this.selectedPerson=p;
  }

  delete(p:Person) {
    this.selectedPerson=p;
    this.serverService.deletePerson(this.selectedPerson.id).subscribe(() => {
      this.update()
    })
  }
   
    

  ngOnInit() {
  }
}
